import { graphql } from 'gatsby';
import React, { useRef, useEffect, useCallback } from 'react';
import Seo from '../components/Seo';
import Layout from '../layout';
import Header from '../components/Header';
import Footer from '../components/Footer';

const Hankyeolk = ({ data }) => {
  const { name, enName } = data.site.siteMetadata.author;

  return (
    <main className='block'>
      <Header />
      <Seo title={name} />
      <section className='hankyeolk-container'>
        <header id='hankyeolk-header' className='hankyeolk-header'>
          <img
            src={'../../hk-icon-white.png'}
            alt='icon'
            className='hankyeolk-icon'
          />
          <h1 className='hankyeolk-name'>
            {name} | {enName}
          </h1>
          <p className='hankyeolk-descript'>
            늘 최신으로 업데이트중인 PM입니다.
          </p>
          <p className='hankyeolk-descript-btn'>
            <b>{name}</b>에 대해서 조금 더 알아보기 😎
          </p>
        </header>
        <section id='hankyeolk-description' className='hankyeolk-main-section'>
          <div id='item1' className='section-item-col'></div>
          <div id='item2' className='section-item-normal'></div>
          <div id='item3' className='section-item-normal'></div>
          <div id='item4' className='section-item-normal'>
            ⚒️ 열심히 만들고 있어요 ⚒️
          </div>
          <div id='item5' className='section-item-normal'></div>
          <div id='item6' className='section-item-normal'></div>
          <div id='item7' className='section-item-row'></div>
        </section>
      </section>
      <Footer />
    </main>
  );
};

export default Hankyeolk;

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        author {
          name
          enName
        }
      }
    }
  }
`;
